<template>
  <v-container>
    <v-layout row>
      <v-flex xs12 sm6 offset-sm3>
        <h4 class="primary--text">CRIAR PRODUTOS</h4>
      </v-flex>
    </v-layout>
    <v-layout row>
      <v-flex xs12>
        <form @submit.prevent="criaProduto">
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field name="title" label="Nome do produto" id="title" required v-model="nome"></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field name="title" label="Categoria" id="title" required v-model="categoria"></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field name="title" label="Preco do produto::AKZ" id="title" required v-model="preco"></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field name="imageUrl" label="Imagem do produto" id="imageUrl" required v-model="imageUrl"></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <img :src="imageUrl" height="150">
            </v-flex>
          </v-layout>
          <v-layout row>
            <v-flex xs12 sm6 offset-sm3>
              <v-text-field name="description" label="Descricao" id="description" required rows v-model="descricao"></v-text-field>
            </v-flex>
          </v-layout>
          <v-layout>
            <v-flex xs12 sm6 offset-sm3>
              <v-btn class="primary" type="submit">CRIAR PRODUTO</v-btn>
            </v-flex>
          </v-layout>
        </form>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data (){
    return{
      nome: '',
      preco: '',
      categoria: '',
      descricao: '',
      imageUrl: ''
    }
  },
  computed: {
    formIsValid (){
      return this.nome != '' 
          && this.preco != ''
          && this.imageUrl != '' 
          && this.descricao != '' 
          && this.categoria != ''
    }
  },
  methods:{
    criaProduto(){
      if(!this.formIsValid){
        return
      }
      const dadosProduto ={
        nome: this.nome,
        preco: this.preco,
        descricao: this.descricao,
        categoria: this.categoria,
        imageUrl: this.imageUrl,
        date: '2018'
      }
      this.$store.dispatch('criarProduto', dadosProduto)
      this.$router.push('/produtos')
    }
  }
}
</script>

<style>

</style>



