import Vue from 'vue'
import Vuex from 'vuex'
import * as firebase from 'firebase'
import { error } from '@firebase/database/dist/esm/src/core/util/util';
Vue.use(Vuex)

export const store = new Vuex.Store({
    state: {
        carregaProduto: [
            
        ],
        user: null,
        carregar: false,
        error: null
    },
    mutations:{
        carregandoProdutos(state, payload){
            state.carregaProduto = payload
        },
        criarProduto(state, payload){
            state.carregaProduto.push(payload)
        },
        updateProduto(state, payload){
            const produto = state.carregaProduto.find(produto => {
                return produto.id === payload.id
            })
            if(payload.nome){
                produto.nome = payload.nome
            }
            if(payload.descricao){
                updateObj.descricao = payload.descricao
            }
            if(payload.preco){
                updateObj.preco = payload.preco
            }
            if(payload.categoria){
                updateObj.categoria = payload.categoria
            }
            if(payload.imageUrl){
                updateObj.imageUrl = payload.imageUrl
            }
        },
        setUser(state, payload){
            state.user = payload
        },
        carregando(state, payload){
            state.loading = payload
        },
        erro(state, payload){
            state.error = payload
        },
        limpaErro(state){
            state.error = null
        }
    },
    actions:{
        CarregaProdutos({commit}){
            commit('carregando', true)
            firebase.database().ref('produtos').once('value').then((data) => {
                const produtos = []
                const obj = data.val()
                for(let key in obj){
                    produtos.push({
                        id: key,
                        nome: obj[key].nome,
                        categoria: obj[key].categoria,
                        descricao: obj[key].descricao,
                        preco: obj[key].preco,
                        imageUrl: obj[key].imageUrl,
                        date: obj[key].date
                    })
                }
                commit('carregandoProdutos', produtos)
                commit('carregando', false)
            }).catch((error)=>{
                console.log('error')
            })
        },
        criarProduto({commit}, payload){
            const produto = {
                nome: payload.nome,
                categoria: payload.categoria,
                imageUrl: payload.imageUrl,
                preco: payload.preco,
                descricao: payload.descricao,
                date: payload.date
            }
            firebase.database().ref('produtos').push(produto).then((data) => {
                const key = data.key
                commit('criarProduto', {
                    ...produto,
                    id: key
                })
            }).catch((error) => {
                console.log(error)
            })
            // Firebase
        },
        updateProduto({commit}, payload){
            commit(carregando, true)
            const updateObj = {}
            if(payload.nome){
                updateObj.nome = payload.nome
            }
            if(payload.descricao){
                updateObj.descricao = payload.descricao
            }
            if(payload.preco){
                updateObj.preco = payload.preco
            }
            if(payload.categoria){
                updateObj.categoria = payload.categoria
            }
            if(payload.imageUrl){
                updateObj.imageUrl = payload.imageUrl
            }
            firebase.database().ref('produtos').child(payload.id).update(updateObj).then(() =>{
                commit('carregando', false)
                commit('updateProduto', payload)
            }).catch(error => {
                commit('carregando', false)
            })
        },
        criarUsuario({commit}, payload){
            commit('carregando', true)
            commit('limpaErro')
            firebase.auth().createUserWithEmailAndPassword(payload.email, payload.senha).then(
                user => {
                    commit('carregando', false)
                    const newUser = {
                        id: user.uid
                    }
                    commit('setUser', newUser)
                },
            )
            .catch(
                error => {
                    commit('carregando', false)
                    commit('erro', error)
                    console.log(error)
                }
            )
        },
        entrarUsuario({commit}, payload){
            commit('carregando', true)
            commit('limpaErro')
            firebase.auth().signInWithEmailAndPassword(payload.email, payload.senha).then(
                user => {
                    commit('carregando', false)
                    const newUser = {
                        id: user.uid
                    }
                    commit('setUser', newUser)
                },
            ).catch(
                error => {
                    commit('carregando', false)
                    commit('erro', error)
                    console.log(error)
                }
            )
        },
        logado({commit}, payload){
            commit('setUser', {id : payload.uid})
        },
        logout({commit}){
            firebase.auth().signOut
            commit('setUser', null)
        },
        limpaErro({commit}){
            commit('limpaErro')
        }
    },
    getters:{
        carregaProdutos(state){
            return state.carregaProduto.sort((produtoA, produtoB) => {
                return produtoA.date > produtoB.date
            })
        },
        nProdutos(state){
            return state.carregaProduto.slice(-3)
        },
        carregaProduto(state){
            return (produtoId) => {
                return state.carregaProduto.find((produto) => {
                    return produto.id === produtoId
                })
            }
        },
        user(state){
            return state.user
        },
        carregando(state){
            return state.carregando
        },
        error(state){
            return state.error
        }
    }
})


