<template>
  <v-container fluid grid-list-md>
      <v-layout row wrap class="white">
          <v-flex d-flex xs12 sm6 md4>
              <div>
                  <h1>Cuidados com sua semijoia</h1>
                  <p>
                      Para manter a qualidade e a durabilidade dos produtos, a Kyaku aconselha
                    alguns cuidados:<br>
                    Armazene em local bem arejado, limpo, seco;
                    Mantenha a semijoia longe de produtos químicos;
                    Evite deixar sua semijoia no calor e na umidade;
                    Nunca ferva ou use qualquer produto químico ou deixe de molho a
                    semijoia para devolver o brilho a peça; <br>
                    Evite o contato com perfumes, cremes, reagentes químicos, loções,
                    pomadas, protetor solar, desodorantes, óleos, tinturas de cabelo, creme de
                    alisamento, ou qualquer outro produto químico, etc.;
                    Tenha cuidado com o uso excessivo do álcool em gel;
                    O suor excessivo tem uma composição química que pode ser corrosivo e
                    escurecer a peça;<br>
                    Contato com a terra e adubos químicos pode escurecer a peça, ao fazer
                    trabalhos de jardinagem, retire suas semijoias;
                    Ao fazer trabalhos manuais. O atrito risca a peça e pode quebrar ou
                    descolar as pedras;<br>
                    Não usar no banho diário, na água do mar, piscina e sauna. A salinidade do
                    mar, o cloro da piscina, xampus e sabonetes podem causar danos às peças.
                    Não usar ao lavar a louça e a roupa, limpar a casa, lavar o carro ou outros
                    serviços domésticos;<br>
                    Nunca use acetona, pasta de dente, álcool para limpar a sua semijoia;
                    Nunca utilize as peças durante a aplicação de tintura de cabelo ou outro
                    produto químico; <br>
                    Nunca tente dar brilho em suas semijoias utilizando esponjas de aço ou
                    qualquer tipo de abrasivo (polidores, pasta de dentes, etc).
                    Tenha cuidado com as pedras, pois uma batida ou queda poderá
                    danificálas;<br>
                    Espere alguns minutos para colocar as semijoias após usar perfumes,
                    cremes, desodorantes;<br>
                    Pessoas que possuem elevado índice de ácido úrico, nervosas ou que
                    estejam fazendo uso de algum medicamento podem escurecer peças de
                    ouro, prata e folheadas; <br>
                    Guardar as peças separadas e embrulhadas em papel de seda, veludo ou
                    camurça evita o atrito de uma peça com outra, evitando os riscos no banho
                    e nas pedras;<br>
                    Antes de guardar as peças que foram usadas, limpe com uma flanela macia;
                    Limpe suas semijoias com um pano seco ou flanela de algodão;
                     Diamantes artificiais devem ser limpos apenas com papel de seda fino;
                    Peças em couro não podem ser secadas ao sol, pois podem rachar. O ideal é
                    que se ficaram úmidas por algum motivo, deixe sacando na sombra. Além
                    disso, materiais naturais como pedras e fibras, são sensíveis e merecem
                    cuidados redobrados. <br>
                    Verifique primeiro se você tem alergia, pois essa não é uma condição para
                    troca ou devolução do valor pago.
                  </p>
              </div>
          </v-flex>
          <v-flex d-flex xs12 sm6 md4>
              <div>
                <h1>Política de Entrega</h1>
                <p>
                    A Kyaku envia seus produtos em todo território nacional, via correios de
                    Angola ou Macom. <br>
                    Compras acima de Kz50.000 (cinquenta mil kwanzas) a entrega e é por
                    conta da Kyaku. <br>
                    Os prazos de entrega informados pela empresa Correios passarão a ser
                    estimados, ou seja, não deverão ser entendidos como compromisso firme
                    de entrega;<br>
                    Confira sempre a nota fiscal e os produtos no ato da entrega e não aceite
                    qualquer embalagem que esteja violada ou que não esteja em condições
                    adequadas. Caso isso ocorra, avise imediatamente a nossa equipe no
                    email kyakubijuterias@gmail.com ou pelo WhatsApp 945799931. <br>
                    Os Correios realizam tentativas de entrega dos produtos, em dias úteis
                    consecutivos. Caso não ocorra a entrega do objeto nas tentativas
                    estipuladas pelo Correio, o produto será devolvido para Kyaku e um novo
                    envio será feito, sendo cobrado do cliente o valor do frete. <br>
                    Os pedidos realizados com cartão de crédito ou transferência bancária, a
                    confirmação do pagamento acontece em até 2 dias úteis, após a
                    confirmação da transação pela operadora.<br>
                    Em caso de dúvidas, entre em contato conosco via Chat, de segunda à
                    sextafeira das 9h às 18h, email ou WhatsApp. <br>
                    Email: kyakubijuterias@gmail.com ou pelo WhatsApp 945799931.
                </p>
              </div>
          </v-flex>
          <v-flex d-flex xs12 sm6 md4>
              <div>
                <h1>Política de Troca</h1>
                <p>
                    Condições para troca, devolução ou reembolso dos produtos:
                    Erro na entrega, produto ou embalagem danificados;<br>
                    Produto com defeito de fábrica; <br>
                    Vale lembrar, que as trocas dependerão da disponibilidade de estoque e
                    algumas peças são exclusivas e temporárias. <br>
                    Não fazemos trocas de peças modificadas. <br>
                    Os pedidos de troca ou devolução serão analisados, antes de seu
                    prosseguimento. <br>
                    Caso a peça tenha algum defeito de fabricação, a troca será feita por uma
                    peça igual, ou do mesmo valor, à escolha do cliente. <br>
                    Havendo necessidade de troca ou devolução, entre em contato conosco via
                    Chat, de segunda à sextafeira das 9h às 18h, email ou WhatsApp. <br>
                    Email: kyakubijuterias@gmail.com
                    WhatsApp 945799931.
                </p>
                <img src="../assets/Fotografia.png" alt="Kyaku Joias">
              </div>
          </v-flex>
      </v-layout>
  </v-container>
</template>

