import Vue from 'vue'

import alerta from './components/Shared/Alerts.vue'
import editar from './components/Produtos/edit/Editar.vue'

import Vuetify from 'vuetify'
import 'vuetify/dist/vuetify.css'
import * as firebase from 'firebase'
import App from './App'
import router from './router'

import {store} from './store'

Vue.use(Vuetify)

Vue.config.productionTip = false

Vue.component('app-edit', editar)
Vue.component('app-alert', alerta)

/* eslint-disable no-new */
new Vue({
  el: '#app',
  router,
  store,
  render: h => h(App),
  created(){
    firebase.initializeApp({
      apiKey: "AIzaSyCu7ZyU9ptsowe1zCOm-G_c1xYm1kmxxhY",
      authDomain: "kyakustore-543b7.firebaseapp.com",
      databaseURL: "https://kyakustore-543b7.firebaseio.com",
      projectId: "kyakustore-543b7",
      storageBucket: "gs://kyakustore-543b7.appspot.com",
      messagingSenderId: "438021359684"
    })
    firebase.auth().onAuthStateChanged((user) => {
      if(user){
        this.$store.dispatch('logado', user)
      }
    })
    this.$store.dispatch('CarregaProdutos')
  }
})



