<template>
  <v-container fluid grid-list-md>
    <v-layout row wrap>
      <v-flex xs12 sm6 class="text-xs-center text-sm-right">
        <v-btn large  to="/produtos" color="amber">Explorar Produtos</v-btn>
      </v-flex>
      <v-flex xs12 sm6 class="text-xs-center text-sm-left">
        <v-btn large  to="/info" color="amber">Informações</v-btn>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex d-flex xs12 sm6 md4 v-for="i in produtos" :key="i.id">
        <v-card hover>
          <v-card-media :src="i.imageUrl" height="300px"></v-card-media>
          <v-card-title>
