            <h3>Telemovel/Whatsapp: 945 799 931</h3>
            <a href="https://www.facebook.com/kyakubijuterias/"><img src="../assets/facebook.png" alt=""></a>
            <a href="https://www.instagram.com/kyaku_joias_acessorios/"><img src="../assets/instagram.png" alt=""></a>
          </v-card-text>
        </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
  export default {
    computed: {
      produtos() {
        return this.$store.getters.nProdutos
      },
      carregando(){
        return this.$store.getters.carregando
      }
  },
    methods: {
      carregandoProduto(id){
        this.$router.push('/produtos/' + id)
      }
    }
}
</script>

<style scoped>
.contacto{
  background-color:white ;
}
</style>



