<template>
  <v-container>
    <v-layout row wrap>
      <v-flex xs8 sm6 offset-sm3>
        <v-list subheader>
          <v-subheader>LISTA DE PRODUTOS</v-subheader>
          <v-list-tile v-for="i in produtos" v-bind:key="i.id">
            <v-list-tile-content>
              <v-list-tile-title>{{i.nome | to-lowercase}}</v-list-tile-title>
            </v-list-tile-content>
            <v-list-tile-content>
              <v-list-tile-title>{{i.categoria}}</v-list-tile-title>
            </v-list-tile-content>
            <v-list-tile-content>
              <v-list-tile-title>{{i.preco}}</v-list-tile-title>
            </v-list-tile-content>
            <v-list-tile-action>
              <v-btn icon @click="eliminar(i.id)">
                <v-icon>delete</v-icon>
              </v-btn>
            </v-list-tile-action>
          </v-list-tile>
        </v-list>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
import * as firebase from 'firebase'

export default {
  data(){
    return{
      chave: this.produtos.id
    }
  },
  computed: {
    produtos(){
      return this.$store.getters.carregaProdutos
    }
  },
  methods:{
    eliminar: (key) => {
      firebase.database().ref('produtos').child(key).remove()
      alert("ELIMINADO COM SUCESSO")
    }
  }
}
</script>

<style>

</style>



