<template>
  <v-container fluid grid-list-md>
    <v-layout row>
      <v-flex xs4>
        <v-subheader>PROCURAR PRODUTOS</v-subheader>
      </v-flex>
      <v-flex xs4>
        <v-text-field name="input-1-3" label="Anel, Puseira, Pasta..." single-line v-model="search">
        </v-text-field>
      </v-flex>
    </v-layout>
    <v-layout row wrap>
      <v-flex d-flex xs12 sm6 md4 v-for="i in produtos" :key="i.id">
        <v-card hover>
          <v-card-media :src="i.imageUrl" height="300px"></v-card-media>
          <v-card-title>
            <div>
                <h3 class="dc">{{i.nome}}</h3>
                <span class="desc">PREÇO</span>
                <h2 class="preco">$ {{i.preco}}</h2>
            </div>
          </v-card-title>
          <v-card-actions>
          <v-btn flat :to="'/produtos/' + i.id" color="amber">Detalhes</v-btn>
        </v-card-actions>
      </v-card>
      </v-flex>
    </v-layout>
  </v-container>
</template>

<script>
export default {
  data(){
    return{
      search: ''
    }
  },  
  computed: {
    produtos(){
      return this.$store.getters.carregaProdutos.filter((produtos) => {
        return produtos.categoria.match(this.search)
      })
    }
  }
}
</script>

<style scoped>
.desc{
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    color: #f1c40f;
}
.dc{
    font-size: 16px;
    color: #2c3e50;
}
.preco{
    font-size: 22px;
    font-style: italics;
}
</style>



