<template>
    <v-alert color="error" icon="warning" dismissible @input="fechando" :value="true">
      {{text}}
    </v-alert>
</template>

<script>
export default {
    props:['text'],
    methods:{
        fechando() {
            this.$emit('dismissed')
        }
    }
}
</script>

<style>

</style>



