import Vue from 'vue'
import Router from 'vue-router'
import Home from '@/components/Home'
import Criar from '@/components/Produtos/Criar'
import Produtos from '@/components/Produtos/Produtos'
import Produto from '@/components/Produtos/Produto'
import Perfil from '@/components/User/Perfil'
import Signin from '@/components/User/Signin'
import Signup from '@/components/User/Signup'
import Info from '@/components/Info'
import AuthGuard from './auth-guard'

Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/produtos',
      name: 'Produtos',
      component: Produtos
    },
    {
      path: '/info',
      name: 'Info',
      component: Info
    },
    {
      path: '/produtos/criar',
      name: 'Criar',
      component: Criar,
      beforeEnter: AuthGuard
    },
    {
      path: '/produtos/:id',
      name: 'Produto',
      props: true,
      component: Produto
    },
    {
      path: '/perfil',
      name: 'Perfil',
      component: Perfil,
      beforeEnter: AuthGuard
    },
    {
      path: '/signup',
      name: 'Signup',
      component: Signup
    },
    {
      path: '/signin',
      name: 'Signin',
      component: Signin
    },
  ],
  mode: 'history'
})



