<template>
  <v-container>
      <v-layout row wrap v-if="carregando">
        <v-flex xs class="text-xs-center">
            <v-progress-circular indeterminate color="amber" :width="7" :size="70" v-if="carregando"></v-progress-circular>
        </v-flex>
    </v-layout>
      <v-layout row wrap v-else>
          <v-flex xs12 sm6 offset-sm3>  
              <v-card hover>
                <v-card-media :src="produtos.imageUrl" height="500px"></v-card-media>
                <v-card-title primary-title>
                    <div class="">
                        <h2>{{produtos.nome}}</h2><br>
                        <span class="desc">DESCRIÇÃO</span>
                        <h2 class="dc">{{produtos.descricao}}</h2><br>
                        <span class="desc">PREÇO</span>
                        <p class="preco">{{produtos.preco}}</p>
                    </div>
                </v-card-title>
                <v-card-text>
                    <span class="desc">CONTACTOS</span>
                    <h3 class="dc">Telemovel/Whatsapp: 945 799 931</h3>
                    <a href="https://www.facebook.com/kyakubijuterias/"><img src="../../assets/facebook.png" alt="" width="32px"></a>
                    <a href="https://www.instagram.com/kyaku_joias_acessorios/"><img src="../../assets/instagram.png" alt="" width="32px"></a>
                </v-card-text>
                <template v-if="usuarioLogado">
                    <v-spacer></v-spacer>
                    <app-edit :produtos = "produtos"></app-edit> 
                </template>
                <v-card-actions>
                    <v-btn flat :to="'/info'" color="amber">Informações</v-btn>
                </v-card-actions>
              </v-card>
          </v-flex>
      </v-layout>
  </v-container>
</template>

<script>
export default {
    props: ['id'],
    computed:{
        produtos () {
            return this.$store.getters.carregaProduto(this.id)
        },
        usuarioLogado(){
            return this.$store.getters.user !== null && this.$store.getters.user !== undefined
        },
        carregando(){
            return this.$store.getters.carregando
        }
    }
}
</script>

<style scoped>
.desc{
    text-align: center;
    font-size: 18px;
    font-weight: bold;
    color: #f1c40f;
}
.dc{
    font-size: 16px;
}
.preco{
    font-size: 22px;
    font-style: italics;
}
</style>



