<template>
  <v-dialog width="350" persistent v-model="editDialog">
      <v-btn fab accent slot="activator">
          <v-icon>edit</v-icon>
      </v-btn>
      <v-card>
          <v-container>
              <v-layout row wrap>
                  <v-flex xs12>
                      <v-card-title>
                          Editar Produto
                      </v-card-title>
                  </v-flex>
              </v-layout>
              <v-divider></v-divider>
              <v-layout row wrap>
                  <v-flex xs12>
                      <v-card-text>
                        <v-text-field name="title" label="Nome do produto" id="title" v-model="nomeE"></v-text-field>
                        <v-text-field name="title" label="Categoria" id="title" v-model="categoriaE"></v-text-field>
                        <v-text-field name="title" label="Preco do produto::AKZ" id="title" v-model="precoE"></v-text-field>
                        <v-text-field name="imageUrl" label="Imagem do produto" id="imageUrl" v-model="imageUrlE"></v-text-field>
                        <v-text-field name="description" label="Descricao" id="description" rows v-model="descricaoE"></v-text-field>
                      </v-card-text>
                  </v-flex>
              </v-layout>
              <v-divider></v-divider>
                <v-layout row wrap>
                    <v-flex xs12>
                        <v-card-actions>
                            <v-btn flat class="blue--text" @click="editDialog = false">FECHAR</v-btn>
                            <v-btn flat class="blue--text" @click="salvar">SALVAR</v-btn>
                        </v-card-actions>
                    </v-flex>
                </v-layout>
          </v-container>
      </v-card>
  </v-dialog>
</template>

<script>
    export default {
        props:['produtos'],
        data(){
            return {
                editDialog: false,
                nomeE: this.produtos.nome,
                categoriaE: this.produtos.categoria,
                precoE: this.produtos.preco,
                imageUrlE: this.produtos.imageUrl,
                descricaoE: this.produtos.descricao
            }
        },
        methods:{
            salvar(){
                if(this.nomeE.trim() == '' 
                    || this.categoriaE.trim() == '' 
                    || this.precoE.trim() == '' 
                    || this.imageUrlE.trim() == '' 
                    || this.descricaoE.trim() == ''){
                    return
                }
                this.editDialog = false
                this.$store.dispatch('updateProduto', {
                    id: this.produtos.id,
                    nome: this.nomeE,
                    categoria: this.categoriaE,
                    preco: this.precoE,
                    imageUrl: this.imageUrlE,
                    descricao: this.descricaoE
                })
            }
        }
    }
</script>

<style>

</style>



